import { Component, OnInit } from "@angular/core";
import { MatInputModule } from "@angular/material/input";
import { Router } from "@angular/router";
import { SharedService } from "../shared/shared.service";
import { LoginService } from "./login.service";
import {
  FormBuilder,
  FormGroup,
  FormControl,
  Validators,
} from "@angular/forms";
import { AuthService } from "@app/core/service/auth.service";
import { Role } from "@app/shared/enums/role.enum";

@Component({
  selector: "app-login",
  templateUrl: "./login.component.html",
  styleUrls: ["./login.component.css"],
})
export class LoginComponent implements OnInit {
  loginForm: FormGroup;
  constructor(
    private router: Router,
    private fb: FormBuilder,
    private loginService: LoginService,
    private authService: AuthService,
    private sharedService: SharedService
  ) {}
  userName: string = "";
  password: string = "";

  ngOnInit(): void {
    this.loginForm = this.fb.group({
      userName: new FormControl({ value: "", disabled: false }, [
        Validators.required,
        Validators.minLength(4),
      ]),
      password: new FormControl({ value: "", disabled: false }, [
        Validators.required,
        Validators.minLength(4),
      ]),
    });
    this.userName = "";
    this.password = "";
    localStorage.removeItem("isLoggedIn");
  }
  onFormSubmit() {
    if (this.loginForm.valid) {
      let objLogin = { email: "", password: "" };
      objLogin.email = this.loginForm.get("userName").value;
      objLogin.password = this.loginForm.get("password").value;
      this.loginService.login(objLogin).subscribe(
        (res) => {
          if (res.statusCode == 10021) {
            this.sharedService.add({
              severity: "error",
              summary: "Error",
              detail: res.message,
            });
          } else if (res.message == "Invalid Credentials") {
            this.sharedService.add({
              severity: "error",
              summary: "Error",
              detail: res.message,
            });
          } else if (res.message == "User Login Successful") {
            this.authService.currentUser = res.responsePayload;
            this.authService.currentUser.role = Role[
              res.responsePayload?.roleId
            ].toLowerCase();
            // this.authService.currentUser.role = this.getRole(
            //   res.responsePayload?.roleId
            // );
            this.authService.login(this.authService.currentUser);
            this.router.navigate([this.authService.currentUser.role]);
          }
        },
        (error) => {
          this.sharedService.add({
            severity: "error",
            summary: "Error",
            detail: error,
          });
        }
      );
    } else {
      this.sharedService.add({
        severity: "error",
        summary: "Error",
        detail: "Username and Password didn't match. Please try again.",
      });
    }
  }
  getRole(roleId: number) {
    if (
      roleId == Role.SupremeAdmin ||
      roleId == Role.Admin ||
      roleId == Role.HR
    ) {
      return Role[Role.Admin].toLowerCase();
    } else if (roleId == Role.Employee) {
      return Role[Role.User].toLowerCase();
    } else {
      return Role[roleId].toLowerCase();
    }
  }
}
