import { HttpClient } from "@angular/common/http";
import { Injectable } from "@angular/core";
import { ApiResponse } from "@app/shared/models/api-response.model";

@Injectable({
  providedIn: "root",
})
export class InvoicesService {
  constructor(private http: HttpClient) {}
  getOrganizationDetails() {
    return this.http.get<ApiResponse<any>>(`api/organization/details`);
  }
  getCompanyList(organizationDetailsId) {
    return this.http.get<ApiResponse<any>>(
      `api/company/list?organizationDetailsId=${organizationDetailsId}`
    );
  }
  getProjectEmployeeList(companyDetailsId) {
    return this.http.get<ApiResponse<any>>(`/api/invoice/projects/employees/company/get/${companyDetailsId}`);
  }
  saveInvoice(invoicesDetails) {
    return this.http.post<ApiResponse<any>>(
      `api/invoice/save`,
      invoicesDetails
    );
  }
  getAllInvoices(obj?:any){
    let params:any ;
    if(obj?.startDate && obj?.endDate && obj?.companyDetailsId ){
      
       params = {startDate:obj?.startDate, endDate:obj?.endDate,companyDetailsId:obj?.companyDetailsId}
    }else  if(obj?.startDate && obj?.endDate ){
      params = {startDate:obj?.startDate, endDate:obj?.endDate}
    }  else  if(obj?.companyDetailsId){
      params = {companyDetailsId:obj?.companyDetailsId}
    } 
    return this.http.get<ApiResponse<any>>(
      `api/invoice/all`,{ 
        params: params
    }
    );
  }
  getInvoiceDetails(invoiceDetailsId) {
    return this.http.get<ApiResponse<any>>(
      `api/invoice/details/get/${invoiceDetailsId}`
    );
  }
  deleteInvoiceEntry(invoiceEntryDetailsId) {
    return this.http.put<ApiResponse<any>>(
      `api/invoice/entry/delete/${invoiceEntryDetailsId}`,
      {}
    );
  }
  addPayments(addPayments) {
    return this.http.post<ApiResponse<any>>(
      `api/payment/save`,
      addPayments
    );
  }
  deleteInvoice(invoiceDetailsId){
    return this.http.put<ApiResponse<any>>(
      `api/invoice/delete/${invoiceDetailsId}`,
      {}
    );
  }
  deleteInvoicePayments(paymentDetailsId){
    return this.http.put<ApiResponse<any>>(
      `api/payment/delete/${paymentDetailsId}`,
      {}
    );
  }
  getPayments(params:any) {
    return this.http.get<ApiResponse<any>>(
      `/api/payment/all`,{ 
        params: params
    }
    );
  }
  getInvoiceApproverList(companyDetailsId, status) {
    return this.http.get<ApiResponse<any>>(
      `api/invoice/approver/company?companyDetailsId=${companyDetailsId}&&status=${status}`
    );
  }
  generateInvoiceNumber(){
    return this.http.get<ApiResponse<any>>(
      `/api/invoice/number/get`
    );
  }
  invoiceNumberExist(params){
    return this.http.get<ApiResponse<any>>(
      `api/invoice/number/exists`,{
        params:params
      }
    );

  }
}
