import { Component, OnInit, Input } from "@angular/core";
import { Gender } from "@app/shared/enums/gender.enum";
import { MaritalStatus } from "@app/shared/enums/maritalstatus.enum";
import {
  Profile,
  Experience,
  Education,
} from "@app/shared/models/profile.model";
import { MyProfileService } from "../myprofile.service";
import { SharedService } from "@app/shared/shared.service";

@Component({
  selector: "app-profile-profile",
  templateUrl: "./profile-profile.component.html",
  styleUrls: ["./profile-profile.component.css"],
})
export class ProfileProfileComponent implements OnInit {
  saveProfile: Profile;
  public profileinfo: string = "none";
  educationInfo: string = "none";
  experienceInfo: string = "none";
  errorLabel: string = "";
  clientErrorMsg: any;
  maritalStatuses: any[] = [];
  reportingToUserNames: any[] = [];
  departments: any[] = [];
  filterDepartments: any[] = [];
  filterDesignation: any[] = [];
  designations: any[] = [];
  genders: any[] = [];
  @Input() userId: any;
  @Input() profile: Profile;
  @Input() user: any;
  @Input() state: any;
  get Gender() {
    return Gender;
  }
  get MaritalStatus() {
    return MaritalStatus;
  }
  constructor(
    private profileService: MyProfileService,
    private sharedService: SharedService
  ) {}

  ngOnInit(): void {
    this.maritalStatuses = [
      { label: "Select Marital status", value: null },
      { label: "Single", value: "1" },
      { label: "Married", value: "2" },
    ];
    this.genders = [
      { label: "Select Gender", value: null },
      { label: "Male", value: "1" },
      { label: "Female", value: "2" },
    ];
    this.reportingToUserNames.push({ label: "Select Reports To", value: null });
    this.profileService.getAllDepartments().subscribe((res) => {
      if (res.responsePayload) {
        res.responsePayload.forEach((value) => {
          this.departments.push({
            label: value.department,
            value: value.departmentDetailsId,
          });
        });
      }
    });
    this.profileService.getAllDesignations().subscribe((res) => {
      if (res.responsePayload) {
        res.responsePayload.forEach((value) => {
          this.designations.push({
            label: value.designation,
            value: value.designationDetailsId,
          });
        });
      }
    });
    this.profileService.getReportingToUsers().subscribe((res) => {
      if (res.responsePayload) {
        res.responsePayload.forEach((value) => {
          this.reportingToUserNames.push({
            label: value.fullName,
            value: value.employeeDetailsId,
          });
        });
      }
    });
    if (this.profile.educations) {
      for (let index = 0; index < this.profile.educations.length; index++) {
        const element = this.profile.educations[index];
        if (element.startingDate) {
          let date: string[] = element.startingDate.split("-");
          element.startingDateFormate = new Date(
            parseInt(date[2]),
            parseInt(date[0]) - 1,
            parseInt(date[1])
          );
        }
        if (element.completionDate) {
          let date: string[] = element.completionDate.split("-");
          element.completionDateFormate = new Date(
            parseInt(date[2]),
            parseInt(date[0]) - 1,
            parseInt(date[1])
          );
        }
      }
    } else {
      this.profile.educations = [];
      let education = Object.assign({}, new Education());
      education.employeeDetailsId = this.userId;
      education.status = 1;
      this.profile.educations.push(education);
    }
    if (this.profile.experiences) {
      for (let index = 0; index < this.profile.experiences.length; index++) {
        const element = this.profile.experiences[index];
        if (element.periodFrom) {
          let date: string[] = element.periodFrom.split("-");
          element.periodFromFormate = new Date(
            parseInt(date[2]),
            parseInt(date[0]) - 1,
            parseInt(date[1])
          );
        }
        if (element.periodTo) {
          let date: string[] = element.periodTo.split("-");
          element.periodToFormate = new Date(
            parseInt(date[2]),
            parseInt(date[0]) - 1,
            parseInt(date[1])
          );
        }
      }
    } else {
      this.profile.experiences = [];
      let experience = Object.assign({}, new Experience());
      experience.employeeDetailsId = this.userId;
      experience.status = 1;
      this.profile.experiences.push(experience);
    }
  }
  getProfile(userId) {
    this.profileService.getProfile(userId).subscribe((res) => {
      if (res.message == "Profile Details") {
        this.profile = res.responsePayload;
        if (this.profile.dateOfBirth) {
          let date: string[] = this.profile.dateOfBirth.split("-");
          this.profile.dateOfBirthFormate = new Date(
            parseInt(date[2]),
            parseInt(date[0]) - 1,
            parseInt(date[1])
          );
        }
        if (this.profile.passportExpDate) {
          let date: string[] = this.profile.passportExpDate.split("-");
          this.profile.passportExpDateFormate = new Date(
            parseInt(date[2]),
            parseInt(date[0]) - 1,
            parseInt(date[1])
          );
        }
        if (this.profile.dateOfJoining) {
          let date: string[] = this.profile.dateOfJoining.split("-");
          this.profile.dateOfJoiningFormate = new Date(
            parseInt(date[2]),
            parseInt(date[0]) - 1,
            parseInt(date[1])
          );
        }
        if (this.profile.educations) {
          for (let index = 0; index < this.profile.educations.length; index++) {
            const element = this.profile.educations[index];
            if (element.startingDate) {
              let date: string[] = element.startingDate.split("-");
              element.startingDateFormate = new Date(
                parseInt(date[2]),
                parseInt(date[0]) - 1,
                parseInt(date[1])
              );
            }
            if (element.completionDate) {
              let date: string[] = element.completionDate.split("-");
              element.completionDateFormate = new Date(
                parseInt(date[2]),
                parseInt(date[0]) - 1,
                parseInt(date[1])
              );
            }
          }
        } else {
          this.profile.educations = [];
          let education = Object.assign({}, new Education());
          education.employeeDetailsId = this.userId;
          education.status = 1;
          this.profile.educations.push(education);
        }
        if (this.profile.experiences) {
          for (
            let index = 0;
            index < this.profile.experiences.length;
            index++
          ) {
            const element = this.profile.experiences[index];
            if (element.periodFrom) {
              let date: string[] = element.periodFrom.split("-");
              element.periodFromFormate = new Date(
                parseInt(date[2]),
                parseInt(date[0]) - 1,
                parseInt(date[1])
              );
            }
            if (element.periodTo) {
              let date: string[] = element.periodTo.split("-");
              element.periodToFormate = new Date(
                parseInt(date[2]),
                parseInt(date[0]) - 1,
                parseInt(date[1])
              );
            }
          }
        } else {
          this.profile.experiences = [];
          let experience = Object.assign({}, new Experience());
          experience.employeeDetailsId = this.userId;
          experience.status = 1;
          this.profile.experiences.push(experience);
        }
        return;
      }
    });
  }
  onProfileSubmit() {
    this.errorLabel = "";
    if (
      this.saveProfile.departmentDetails &&
      this.saveProfile.departmentDetails instanceof Object
    ) {
      this.saveProfile.departmentDetailsId = this.saveProfile.departmentDetails.value;
    } else if (this.saveProfile.departmentDetails) {
      this.saveProfile.departmentDetailsId = 0;
      this.saveProfile.department = this.saveProfile.departmentDetails;
    }
    if (
      this.saveProfile.designationDetails &&
      this.saveProfile.designationDetails instanceof Object
    ) {
      this.saveProfile.designationDetailsId = this.saveProfile.designationDetails.value;
    } else if (this.saveProfile.designationDetails) {
      this.saveProfile.designationDetailsId = 0;
      this.saveProfile.designation = this.saveProfile.designationDetails;
    }
    if (this.saveProfile.dateOfBirthFormate) {
      let day = this.saveProfile.dateOfBirthFormate.getDate();
      let month = this.saveProfile.dateOfBirthFormate.getMonth() + 1; // add 1 because months are indexed from 0
      let year = this.saveProfile.dateOfBirthFormate.getFullYear();
      this.saveProfile.dateOfBirth = month + "-" + day + "-" + year;
    }
    if (this.saveProfile.passportExpDateFormate) {
      let day = this.saveProfile.passportExpDateFormate.getDate();
      let month = this.saveProfile.passportExpDateFormate.getMonth() + 1; // add 1 because months are indexed from 0
      let year = this.saveProfile.passportExpDateFormate.getFullYear();
      this.saveProfile.passportExpDate = month + "-" + day + "-" + year;
    }
    if (this.saveProfile.dateOfJoiningFormate) {
      let day = this.saveProfile.dateOfJoiningFormate.getDate();
      let month = this.saveProfile.dateOfJoiningFormate.getMonth() + 1; // add 1 because months are indexed from 0
      let year = this.saveProfile.dateOfJoiningFormate.getFullYear();
      this.saveProfile.dateOfJoining = month + "-" + day + "-" + year;
    }
    this.profileService.saveProfile(this.saveProfile).subscribe(
      (res) => {
        if (res.statusCode == 200) {
          this.sharedService.add({
            severity: "success",
            summary: "Success",
            detail: res.message,
          });
          this.closeAddmodal();
          this.getProfile(this.userId);
        } else {
          this.errorLabel = res.message;
        }
      },
      (errror) => {
        this.errorLabel = errror;
      }
    );
  }
  editprofile(type?) {
    this.saveProfile = Object.assign({}, this.profile);
    this.profileinfo = "block";
    if (type == "emergencyContact") {
      setTimeout(() => {
        document.getElementById("primaryContactName")?.focus();
      }, 100);
    } else if (type == "personalInfo") {
      setTimeout(() => {
        document.getElementById("passportNo")?.focus();
      }, 100);
    }
    this.hiddenScroll();
  }
  experienceClick() {
    this.experienceInfo = "block";
    this.hiddenScroll();
  }
  educationInfoClick() {
    this.educationInfo = "block";
    this.hiddenScroll();
  }
  onEducationSubmit() {
    for (let index = 0; index < this.profile.educations.length; index++) {
      const element = this.profile.educations[index];
      if (element.completionDateFormate) {
        let day = element.completionDateFormate.getDate();
        let month = element.completionDateFormate.getMonth() + 1; // add 1 because months are indexed from 0
        let year = element.completionDateFormate.getFullYear();
        element.completionDate = month + "-" + day + "-" + year;
      }
      if (element.startingDateFormate) {
        let day = element.startingDateFormate.getDate();
        let month = element.startingDateFormate.getMonth() + 1; // add 1 because months are indexed from 0
        let year = element.startingDateFormate.getFullYear();
        element.startingDate = month + "-" + day + "-" + year;
      }
    }
    this.errorLabel = "";
    this.profileService.saveEducations(this.profile.educations).subscribe(
      (res) => {
        if (res.statusCode == 200) {
          this.sharedService.add({
            severity: "success",
            summary: "Success",
            detail: res.message,
          });
          this.closeAddmodal();
        } else {
          this.errorLabel = res.message;
        }
      },
      (error) => {
        this.errorLabel = error;
      }
    );
  }

  onExperienceSubmit() {
    for (let index = 0; index < this.profile.experiences.length; index++) {
      const element = this.profile.experiences[index];
      if (element.periodFromFormate) {
        let day = element.periodFromFormate.getDate();
        let month = element.periodFromFormate.getMonth() + 1; // add 1 because months are indexed from 0
        let year = element.periodFromFormate.getFullYear();
        element.periodFrom = month + "-" + day + "-" + year;
      }
      if (element.periodToFormate) {
        let day = element.periodToFormate.getDate();
        let month = element.periodToFormate.getMonth() + 1; // add 1 because months are indexed from 0
        let year = element.periodToFormate.getFullYear();
        element.periodTo = month + "-" + day + "-" + year;
      }
    }
    this.errorLabel = "";
    this.profileService.saveExperiences(this.profile.experiences).subscribe(
      (res) => {
        if (res.statusCode == 200) {
          this.sharedService.add({
            severity: "success",
            summary: "Success",
            detail: res.message,
          });
          this.closeAddmodal();
        } else {
          this.errorLabel = res.message;
        }
      },
      (error) => {
        this.errorLabel = error;
      }
    );
  }
  onAddMoreEducation() {
    let education = Object.assign({}, new Education());
    education.employeeDetailsId = this.userId;
    education.status = 1;
    this.profile.educations.push(education);
  }
  onAddMoreExperience() {
    let experience = Object.assign({}, new Experience());
    experience.employeeDetailsId = this.userId;
    experience.status = 1;
    this.profile.experiences.push(experience);
  }

  onRemoveExperience(experience: Experience) {
    if (this.profile.experiences.length > 1) {
      let index = this.profile.experiences.indexOf(experience);
      this.profile.experiences.splice(index, 1);
    }
  }
  onRemoveEducation(education: Education) {
    if (this.profile.educations.length > 1) {
      let index = this.profile.educations.indexOf(education);
      this.profile.educations.splice(index, 1);
    }
  }
  saveEducations(educations: Education[]) {
    // this.profileService
    //   .saveEducations(this.profile.educations)
    //   .subscribe((res) => {
    //     if (res.message == "Educations") {
    //       this.profile = res.responsePayload;
    //     } else if (
    //       res.message ==
    //       `Profile Details not found for the employeeDetailsId :${this.user.employeeDetailsId}`
    //     ) {
    //     }
    //   });
  }
  saveExperiences(experiences: Experience[]) {
    // this.profileService
    //   .saveExperiences(this.profile.experiences)
    //   .subscribe((res) => {
    //     if (res.message == "Experiences saved successfully") {
    //       this.profile = res.responsePayload;
    //     } else if (
    //       res.message ==
    //       `Profile Details not found for the employeeDetailsId :${this.user.employeeDetailsId}`
    //     ) {
    //     }
    //   });
  }

  departmentsList($event) {
    let query = $event.query;
    // let filtered : any[] = [];
    this.filterDepartments = [];
    for (let i = 0; i < this.departments.length; i++) {
      let country = this.departments[i];
      if (country.label.toLowerCase().indexOf(query.toLowerCase()) == 0) {
        this.filterDepartments.push(country);
      }
    }
    console.log(this.filterDepartments);
  }

  designationList($event) {
    let query = $event.query;
    // let filtered : any[] = [];
    this.filterDesignation = [];
    for (let i = 0; i < this.designations.length; i++) {
      let designation = this.designations[i];
      if (designation.label.toLowerCase().indexOf(query.toLowerCase()) == 0) {
        this.filterDesignation.push(designation);
      }
    }
    console.log(this.filterDesignation);
  }

  hiddenScroll() {
    try {
      let bodyElement = document.getElementById("modalbody") as HTMLElement;
      if (bodyElement) {
        bodyElement.classList.add("modal-open");
      }
    } catch (ex) {
      this.clientErrorMsg(ex, "hiddenScroll");
    }
  }
  displayScroll() {
    try {
      let bodyElement = document.getElementById("modalbody") as HTMLElement;
      if (bodyElement) {
        bodyElement.classList.remove("modal-open");
      }
    } catch (ex) {
      this.clientErrorMsg(ex, "displayScroll");
    }
  }

  closeAddmodal() {
    this.profileinfo = "none";
    this.educationInfo = "none";
    this.experienceInfo = "none";
    this.errorLabel = "";
    this.displayScroll();
  }
}
