import { Component, OnInit, Input } from "@angular/core";
import { W4 } from "@app/shared/models/w4-module.model";
import { SharedService } from "@app/shared/shared.service";
import { HttpResponse } from "@angular/common/http";
import { AuthService } from "@app/core/service/auth.service";
import { FormControl, FormGroup } from "@angular/forms";
import { FileUploadModel } from "@app/shared/models/fileUpload.model";
import { W4Service } from "@app/core/custommodules/w4/w4.service";

@Component({
  selector: "app-profile-w4",
  templateUrl: "./profile-w4.component.html",
  styleUrls: ["./profile-w4.component.css"],
})
export class ProfileW4Component implements OnInit {
  @Input() userId: any;
  form: FormGroup;
  clientErrorMsg: any;
  editType: string = "View";
  userName: string;
  w4: W4;
  loading: boolean = false;
  errLabel: string;
  isRecidence: boolean = false;
  fileUpload: FileUploadModel;
  public uploadDisplay: string = "none";
  blob: Blob;
  calculation_method: any = [
    { label: "Prior to 2020 W-4", value: 1 },
    { label: "Current W4", value: 2 },
  ];
  code_filling_status: any = [
    { label: "M", value: "M" },
    { label: "S", value: "S" },
    { label: "NRA", value: "NRA" },
    { label: "NRM", value: "NRM" },
    { label: "H", value: "H" },
  ];
  multiple_jobs: any = [
    { label: "No", value: "0" },
    { label: "Yes", value: "1" },
  ];
  noOfAlertsList: any = [
    { label: "Select Days", value: null },
    { label: "3 Days", value: 3 },
    { label: "7 Days", value: 7 },
    { label: "10 Days", value: 10 },
    { label: "30 Days", value: 30 },
    { label: "90 Days", value: 90 },
    { label: "180 Days", value: 180 },
  ];
  constructor(
    private w4Services: W4Service,
    private sharedService: SharedService,
    private authService: AuthService
  ) {}

  ngOnInit(): void {
    this.w4 = new W4();
    this.fileUpload = new FileUploadModel();
    this.userName =
      this.authService.currentUser.fistName +
      " " +
      this.authService.currentUser.lastName;
    if (this.userId) {
      this.getW4(this.userId);
    }
  }
  getW4(userId) {
    this.w4Services.getW4Details(userId).subscribe(
      (res) => {
        this.loading = false;
        if (res.statusCode == 200) {
          this.w4 = res.responsePayload as W4;
          if (res.responsePayload.nonRecidentAlien === "0") {
            this.isRecidence = false;
          } else {
            this.isRecidence = true;
          }
        } else {
          this.w4.calculationMethod = 2;
        }
      },
      (error) => {
        this.loading = false;
      }
    );
  }
  save() {
    this.w4.employeeDetailsId = this.userId;
    if (this.isRecidence) {
      this.w4.nonRecidentAlien = "1";
    } else {
      this.w4.nonRecidentAlien = "0";
    }
    if (this.w4.w4DetailsId != null || this.w4.w4DetailsId != undefined) {
      this.w4Services.updateW4(this.w4).subscribe(
        (res) => {
          if (res.statusCode == 200) {
            this.sharedService.add({
              severity: "success",
              summary: "Success",
              detail: res.message,
            });
            this.editType = "View";
          } else {
            this.errLabel = res.message;
          }
        },
        (error) => {
          this.errLabel = error;
        }
      );
    } else {
      this.w4Services.addW4(this.w4).subscribe(
        (res) => {
          if (res.statusCode == 200) {
            this.sharedService.add({
              severity: "success",
              summary: "Success",
              detail: res.message,
            });
            this.editType = "View";
          } else {
            this.errLabel = res.message;
          }
        },
        (error) => {
          this.errLabel = error;
        }
      );
    }
  }
  saveFile() {
    const formModel = this.prepareSave();
    if (this.fileUpload.fileToUpload) {
      this.w4Services.uploadW4Doc(formModel).subscribe(
        (res) => {
          if (res.statusCode == 200) {
            this.sharedService.add({
              severity: "success",
              summary: "Success",
              detail: res.message,
            });
            this.closeAddmodal();
          } else {
            this.errLabel = res.message;
          }
        },
        (error) => {
          this.errLabel = error;
        }
      );
    } else {
      this.errLabel = "Please Select a File";
      this.sharedService.add({
        severity: "error",
        summary: "Error!",
        detail: this.errLabel,
      });
    }
  }
  showType() {
    this.editType = "Edit";
  }
  cancel() {
    this.editType = "View";
  }
  showDialogToUpload() {
    this.uploadDisplay = "block";
    this.hiddenScroll();
  }

  downloadW4Form(employeeDocumentUploadDetailsId) {
    this.sharedService.add({
      severity: "info",
      summary: "Info!",
      detail: "File is preparing to download please wait",
    });
    this.w4Services
      .downloadW4Doc(employeeDocumentUploadDetailsId)
      .subscribe((resp: HttpResponse<Blob>) => {
        const blob = new Blob([resp.body], { type: resp.body.type });
        const url = window.URL.createObjectURL(blob);
        var link = document.createElement("a");
        link.href = url;
        let imageType = resp.body.type.substring(
          resp.body.type.lastIndexOf("/") + 1
        );
        link.download = `w4Document_${employeeDocumentUploadDetailsId}.${imageType}`;
        link.click();
        link.remove();
        this.sharedService.add({
          severity: "success",
          summary: "Success",
          detail: "File downloaded",
        });
      });
  }
  getCalculationMethod(data) {
    var calculation_method = "";
    this.calculation_method.forEach((element) => {
      if (element.value == parseInt(data)) {
        calculation_method = element.label;
      }
    });
    return calculation_method;
  }
  getMultipleJobs(data) {
    var multiple_jobs = "";
    this.multiple_jobs.forEach((element) => {
      if (element.value == data) {
        multiple_jobs = element.label;
      }
    });
    return multiple_jobs;
  }
  hiddenScroll() {
    try {
      let bodyElement = document.getElementById("modalbody") as HTMLElement;
      if (bodyElement) {
        bodyElement.classList.add("modal-open");
      }
    } catch (ex) {
      this.clientErrorMsg(ex, "hiddenScroll");
    }
  }
  closeAddmodal() {
    this.uploadDisplay = "none";
    this.errLabel = "";
    this.fileUpload = new FileUploadModel();
    this.displayScroll();
  }
  displayScroll() {
    try {
      let bodyElement = document.getElementById("modalbody") as HTMLElement;
      if (bodyElement) {
        bodyElement.classList.remove("modal-open");
      }
    } catch (ex) {
      this.clientErrorMsg(ex, "displayScroll");
    }
  }
  onFileChange(event) {
    if (event.target.files.length > 0) {
      let file = event.target.files[0];
      this.fileUpload.fileToUpload = file;
    }
  }
  private prepareSave(): any {
    let input = new FormData();
    if (this.fileUpload.checked) {
      this.fileUpload.alertRequired = "1";
    } else {
      this.fileUpload.alertRequired = "0";
    }
    input.append("documentTitle", this.fileUpload.documentTitle);
    input.append("documentNumber", this.fileUpload.documentNumber);
    input.append("fileToUpload", this.fileUpload.fileToUpload);
    input.append("documentExpiryDate", this.fileUpload.documentExpiryDate);
    input.append("alertRequired", this.fileUpload.alertRequired);
    input.append("alertBeforeNoOfDays", this.fileUpload.alertBeforeNoOfDays);
    input.append("employeeDetailsId", this.userId);
    return input;
  }
}
