import { Injectable } from "@angular/core";
import { Router } from "@angular/router";
import { User } from "@app/shared/models/user.model";
import { HttpClient } from "@angular/common/http";
import { ApiResponse } from "@app/shared/models/api-response.model";
import { SharedService } from "@app/shared/shared.service";

@Injectable({
  providedIn: "root",
})
export class AuthService {
  constructor(
    private http: HttpClient,
    private router: Router,
    private sharedService: SharedService
  ) {}
  isLoggedIn = false;
  currentUser: User;
  // store the URL so we can redirect after logging in
  redirectUrl: string;

  login(user: User) {
    sessionStorage.setItem("currentUser", JSON.stringify(user));
    sessionStorage.setItem("isLoggedIn", "true");
    this.isLoggedIn = true;
  }
  isUserLoggedIn() {
    let currentUser = JSON.parse(sessionStorage.getItem("currentUser")) as User;
    if (currentUser != null && currentUser != new User()) {
      this.currentUser = currentUser;
      this.isLoggedIn = true;
      return true;
    }
    return false;
  }
  logout(): void {
    this.http
      .post<ApiResponse<User>>("api/employee/logout", this.currentUser)
      .subscribe((res) => {
        if (res.message == "Logout Successful") {
          sessionStorage.removeItem("currentUser");
          this.isLoggedIn = false;
          this.currentUser = null;
          this.sharedService.add({
            severity: "success",
            summary: "Success",
            detail: "You have been logged out of the system.",
          });
          this.router.navigate(["/login"]);
        } else {
          this.sharedService.add({
            severity: "error",
            summary: "Error",
            detail: res.message,
          });
        }
      });
  }
}
