import { ChangeDetectorRef, Component, OnInit } from '@angular/core';
import { AuthService } from '@app/core/service/auth.service';
import { SharedService } from '@app/shared/shared.service';
import { PaymentService } from './payments.service';

@Component({
  selector: 'app-payments',
  templateUrl: './payments.component.html',
  styleUrls: ['./payments.component.css']
})
export class PaymentsComponent implements OnInit {
  loading: boolean = false;
  payment:Payment
  clientErrorMsg: any;
  customerOptions: any;
  selectedCustomerOptions: any;
  public addPaymentsModel: string = "none";
  public confirmModel: string = "none";
  seletctedPayment:Payment
  organizationDetailsId:any
  toDate: string;
  fromDate: string;
  invoiceNumber:string
  cols = [
    { field: "invoiceNumber", header: "Invoice ID" },
    { field: "companyName", header: "Client Name" },
    { field: "paymentType", header: "Payment Type" },
    { field: "dateReceived", header: "Paid Date" },
    { field: "amount", header: "Paid Amount" },
  ];
  paymentList:any[]
  errorLabel: string;
  constructor(private paymentService:PaymentService,  private authService: AuthService,private sharedService:SharedService,private cdr:ChangeDetectorRef ) { }

  ngOnInit(): void {
    this.payment = new Payment();
    this.organizationDetailsId=this.authService.currentUser.organizationDetailsId
    this.getPayments(Object)
    this.getCompanyList(this.organizationDetailsId)   
  }
  search(fromDate,toDate,invoiceNumber,selectedClientOptions){
    let searchByCompany=null
    if (selectedClientOptions) {
      searchByCompany = selectedClientOptions.companyDetailsId;
    }
    if(fromDate && (toDate==null||toDate==undefined)){
      this.sharedService.add({
        severity: "error",
        summary: "Error",
        detail: "Please select end Date",
      });
    }
    if(toDate && (fromDate==null||fromDate==undefined)){
      this.sharedService.add({
        severity: "error",
        summary: "Error",
        detail: "Please select from Date",
      });
    }  
    console.log(searchByCompany)
      let params:Object ;
    if(fromDate && toDate && invoiceNumber  && searchByCompany){      
      params = {startDate:fromDate, endDate:toDate,invoiceNumber:invoiceNumber,companyDetailsId:searchByCompany}
   }else if(fromDate && toDate && invoiceNumber ){      
       params = {startDate:fromDate, endDate:toDate,invoiceNumber:invoiceNumber}
    }else  if(fromDate && toDate ){
      params = {startDate:fromDate, endDate:toDate}
    }  else  if(invoiceNumber){
      params = {invoiceNumber:invoiceNumber}
    } else if(searchByCompany){
      params = {companyDetailsId:searchByCompany}
    }
    this.getPayments(params)
  }
  getPayments(obj){
    this.paymentService.getPayments(obj).subscribe((res) => {
      if ((res.statusCode = 200)) {
        this.paymentList = res.responsePayload;
      } else {
        this.paymentList = [];
      }
    });
  }
  getCompanyList(organizationDetailsId) {
    return new Promise ((resolve,reject)=>{
      this.paymentService.getCompanyList(organizationDetailsId)
      .subscribe((res) => {
        if (res.statusCode == 200) {
          this.customerOptions = res.responsePayload;
          this.customerOptions.splice(0, 0, {
            companyName: "Select",
            value: null,
          });
          resolve()
        }
      });
    })
   
  }
  addPayments(){
    this.addPaymentsModel='block'    
    this.hiddenScroll();
    
  }
  savePayments(){
    this.payment.companyDetailsId = this.selectedCustomerOptions?.companyDetailsId;
    this.paymentService.addPayments(this.payment).subscribe((res) => {
      if (res.statusCode == 200) {
        this.getPayments(Object)
        this.closeModal()
        this.sharedService.add({
          severity: "success",
          summary: "Success",
          detail: res.message,
        });
       
      } else {
        this.sharedService.add({
          severity: "error",
          summary: "Error",
          detail: res.message,
        });
      }
    });

  }
   async editPayment(rowData){
     this.payment = new Payment()
         this.addPaymentsModel='block'
    await this.getCompanyList(this.organizationDetailsId)
    
    let selectedCustomerOptions = this.customerOptions.filter(res=>{
      if(res.companyDetailsId == rowData.companyDetailsId){
        return res
      }   
    })
    this.selectedCustomerOptions= selectedCustomerOptions[0]
    this.payment=rowData
    this.cdr.detectChanges()
    this.hiddenScroll();

  }
  onDeletePayment(rowData){
    this.confirmModel='block'
    this.seletctedPayment=rowData
    this.hiddenScroll();
  }
  onCompanyList(event){
    

  }
  confirmDeletePayment(seletctedPayment){
    console.log(seletctedPayment)
    this.paymentService.deleteInvoicePayments(seletctedPayment.paymentDetailsId).subscribe(res=>{
      if(res.statusCode==200){        
        this.sharedService.add({
          severity: "success",
          summary: "Success",
          detail: res.message,
        });
      }
    })
    this.closeModal()
  }
  closeModal() {
    this.addPaymentsModel = "none";
    this.confirmModel = "none";
    this.errorLabel = "";
    this.displayScroll();
    this.getPayments(Object)
  }
  displayScroll() {
    try {
      let bodyElement = document.getElementById("modalbody") as HTMLElement;
      if (bodyElement) {
        bodyElement.classList.remove("modal-open");
      }
    } catch (ex) {
      this.clientErrorMsg(ex, "displayScroll");
    }
  }
  hiddenScroll() {
    try {
      let bodyElement = document.getElementById("modalbody") as HTMLElement;
      if (bodyElement) {
        bodyElement.classList.add("modal-open");
      }
    } catch (ex) {
      this.clientErrorMsg(ex, "hiddenScroll");
    }
  }

}
export class Payment {
  paymentDetailsId:number
  companyDetailsId:number
  companyName:string
  paymentType:string
  dateReceived:string
  amount:number
  description:string
  invoiceNumber:string
  employeeDetailsId:string
  employeeName:string
  status:string
}
