import { Component, OnInit, Input } from "@angular/core";
import { HttpResponse } from "@angular/common/http";
import { SharedService } from "@app/shared/shared.service";
import { ActivatedRoute } from "@angular/router";
import { AuthService } from "@app/core/service/auth.service";
import { I9 } from "@app/shared/models/i9-module.model";
import { FormControl } from "@angular/forms";
import { FileUploadModel } from "@app/shared/models/fileUpload.model";
import { I9Service } from "@app/core/custommodules/i9/i9.services";

@Component({
  selector: "app-profile-i9",
  templateUrl: "./profile-i9.component.html",
  styleUrls: ["./profile-i9.component.css"],
})
export class ProfileI9Component implements OnInit {
  userName: string;
  editType: string = "View";
  @Input() userId: any;
  errorLabel: string = "";
  i9: I9;
  errLabel: string;
  uploadDisplay: string = "none";
  clientErrorMsg: any;
  noOfAlertsList: any = [
    { label: "Select Days", value: null },
    { label: "3 Days", value: 3 },
    { label: "7 Days", value: 7 },
    { label: "10 Days", value: 10 },
    { label: "30 Days", value: 30 },
    { label: "90 Days", value: 90 },
    { label: "180 Days", value: 180 },
  ];
  fileUpload: FileUploadModel;
  constructor(
    private sharedService: SharedService,
    private authService: AuthService,
    private activatedRoute: ActivatedRoute,
    private i9Services: I9Service
  ) {}

  ngOnInit(): void {
    this.i9 = new I9();
    this.fileUpload = new FileUploadModel();
    if (this.userId) {
      this.getI9(this.userId);
    }
  }
  getI9(userId) {
    this.i9Services.getI9Details(userId).subscribe((res) => {
      if (res.statusCode == 200) {
        this.i9 = res.responsePayload;
        this.userName = this.i9.firstName + " " + this.i9.lastName;
        if (this.i9.citizenOfUs == "0") {
          this.i9.hasCitizenOfUs = false;
        } else {
          this.i9.hasCitizenOfUs = true;
        }
        if (this.i9.nonCitizenNationalOfUs == "0") {
          this.i9.hasNonCitizenNationalOfUs = false;
        } else {
          this.i9.hasNonCitizenNationalOfUs = true;
        }
        if (this.i9.lawfulPermanentResident == "0") {
          this.i9.hasLawfulPermanentResident = false;
        } else {
          this.i9.hasLawfulPermanentResident = true;
        }
        if (this.i9.alienAuthorizedToWork == "0") {
          this.i9.hasAlienAuthorizedToWork = false;
        } else {
          this.i9.hasAlienAuthorizedToWork = true;
        }
        if (this.i9.dateOfBirth) {
          let date: string[] = this.i9.dateOfBirth.split("-");
          this.i9.dateOfBirthFormat = new Date(
            parseInt(date[2]),
            parseInt(date[0]) - 1,
            parseInt(date[1])
          );
        }
        if (this.i9.alienAuthorizationWorkExpirationDate) {
          let date: string[] = this.i9.alienAuthorizationWorkExpirationDate.split(
            "-"
          );
          this.i9.alienAuthorizationWorkExpirationDateFormat = new Date(
            parseInt(date[2]),
            parseInt(date[0]) - 1,
            parseInt(date[1])
          );
        }
      }
    });
  }

  save() {
    this.i9.employeeDetailsId = this.userId;
    if (this.i9.hasCitizenOfUs) {
      this.i9.citizenOfUs = "1";
    } else {
      this.i9.citizenOfUs = "0";
    }
    if (this.i9.hasNonCitizenNationalOfUs) {
      this.i9.nonCitizenNationalOfUs = "1";
    } else {
      this.i9.nonCitizenNationalOfUs = "0";
    }
    if (this.i9.hasLawfulPermanentResident) {
      this.i9.lawfulPermanentResident = "1";
    } else {
      this.i9.lawfulPermanentResident = "0";
      this.i9.lprAlienRegNoOrUscisNo = "";
    }
    if (this.i9.hasAlienAuthorizedToWork) {
      this.i9.alienAuthorizedToWork = "1";
    } else {
      this.i9.alienAuthorizedToWork = "0";
      this.i9.alienAuthorizationWorkExpirationDateFormat = undefined;
      this.i9.alienRegNoOrUscisNo = undefined;
      this.i9.formI94AdmnNumber = undefined;
      this.i9.foreignPassportNumber = undefined;
      this.i9.countryOfIssuance = undefined;
    }
    if (this.i9.dateOfBirthFormat) {
      let day = this.i9.dateOfBirthFormat.getDate();
      let month = this.i9.dateOfBirthFormat.getMonth() + 1; // add 1 because months are indexed from 0
      let year = this.i9.dateOfBirthFormat.getFullYear();
      this.i9.dateOfBirth = month + "-" + day + "-" + year;
    } else {
      this.i9.dateOfBirth = undefined;
    }
    if (this.i9.alienAuthorizationWorkExpirationDateFormat) {
      let day = this.i9.alienAuthorizationWorkExpirationDateFormat.getDate();
      let month =
        this.i9.alienAuthorizationWorkExpirationDateFormat.getMonth() + 1; // add 1 because months are indexed from 0
      let year = this.i9.alienAuthorizationWorkExpirationDateFormat.getFullYear();
      this.i9.alienAuthorizationWorkExpirationDate =
        month + "-" + day + "-" + year;
    } else {
      this.i9.alienAuthorizationWorkExpirationDate = undefined;
    }
    if (this.i9.i9DetailsId != null || this.i9.i9DetailsId != undefined) {
      this.i9Services.updateI9(this.i9).subscribe(
        (res) => {
          if (res.statusCode == 200) {
            this.sharedService.add({
              severity: "success",
              summary: "Success",
              detail: res.message,
            });
            this.editType = "View";
          } else {
            this.sharedService.add({
              severity: "error",
              summary: "Error!",
              detail: res.message,
            });
          }
        },
        (error) => {
          this.sharedService.add({
            severity: "error",
            summary: "Error!",
            detail: error,
          });
        }
      );
    } else {
      this.i9Services.addI9(this.i9).subscribe(
        (res) => {
          if (res.statusCode == 200) {
            this.sharedService.add({
              severity: "success",
              summary: "Success",
              detail: res.message,
            });
            this.editType = "View";
          } else {
            this.sharedService.add({
              severity: "error",
              summary: "Error!",
              detail: res.message,
            });
          }
        },
        (error) => {
          this.sharedService.add({
            severity: "error",
            summary: "Error!",
            detail: error,
          });
        }
      );
    }
  }
  saveFile() {
    const formModel = this.prepareSave();
    if (this.fileUpload.fileToUpload) {
      this.i9Services.uploadI9Doc(formModel).subscribe(
        (res) => {
          if (res.statusCode == 200) {
            this.closeAddmodal();
            this.sharedService.add({
              severity: "success",
              summary: "Success",
              detail: res.message,
            });
          } else {
            this.sharedService.add({
              severity: "error",
              summary: "Error!",
              detail: res.message,
            });
          }
        },
        (error) => {
          this.errLabel = error;
        }
      );
    } else {
      this.errLabel = "Please Select a File";
      this.sharedService.add({
        severity: "error",
        summary: "Error!",
        detail: this.errLabel,
      });
    }
  }
  onFileChange(event) {
    if (event.target.files.length > 0) {
      let file = event.target.files[0];
      this.fileUpload.fileToUpload = file;
    }
  }
  private prepareSave(): any {
    let input = new FormData();
    if (this.fileUpload.checked) {
      this.fileUpload.alertRequired = "1";
    } else {
      this.fileUpload.alertRequired = "0";
    }
    input.append("documentTitle", this.fileUpload.documentTitle);
    input.append("documentNumber", this.fileUpload.documentNumber);
    input.append("fileToUpload", this.fileUpload.fileToUpload);
    input.append("documentExpiryDate", this.fileUpload.documentExpiryDate);
    input.append("alertRequired", this.fileUpload.alertRequired);
    input.append("alertBeforeNoOfDays", this.fileUpload.alertBeforeNoOfDays);
    input.append("employeeDetailsId", this.userId);
    return input;
  }
  showDialogToUpload() {
    this.uploadDisplay = "block";
    this.hiddenScroll();
  }
  getCalenderFormate(data: string) {
    let date: string[] = data.split("-");
    return new Date(
      parseInt(date[2]),
      parseInt(date[0]) - 1,
      parseInt(date[1])
    );
  }
  parseInt(data) {
    return parseInt(data);
  }
  downloadI9Form(employeeDocumentUploadDetailsId) {
    this.sharedService.add({
      severity: "info",
      summary: "Info!",
      detail: "File is preparing to download please wait",
    });
    this.i9Services
      .downloadI9Doc(employeeDocumentUploadDetailsId)
      .subscribe((resp: HttpResponse<Blob>) => {
        const blob = new Blob([resp.body], { type: resp.body.type });
        const url = window.URL.createObjectURL(blob);
        var link = document.createElement("a");
        link.href = url;
        let imageType = resp.body.type.substring(
          resp.body.type.lastIndexOf("/") + 1
        );
        link.download = `i9Document_${employeeDocumentUploadDetailsId}.${imageType}`;
        link.click();
        link.remove();
        this.sharedService.add({
          severity: "success",
          summary: "Success",
          detail: "File downloaded",
        });
      });
  }
  showType() {
    this.editType = "Edit";
  }
  cancel() {
    this.editType = "View";
  }
  checkType(data) {
    if (data == "1") {
      return "Yes";
    } else {
      return "No";
    }
  }
  hiddenScroll() {
    try {
      let bodyElement = document.getElementById("modalbody") as HTMLElement;
      if (bodyElement) {
        bodyElement.classList.add("modal-open");
      }
    } catch (ex) {
      this.clientErrorMsg(ex, "hiddenScroll");
    }
  }
  displayScroll() {
    try {
      let bodyElement = document.getElementById("modalbody") as HTMLElement;
      if (bodyElement) {
        bodyElement.classList.remove("modal-open");
      }
    } catch (ex) {
      this.clientErrorMsg(ex, "displayScroll");
    }
  }
  closeAddmodal() {
    this.uploadDisplay = "none";
    this.errLabel = "";
    this.fileUpload = new FileUploadModel();
    this.displayScroll();
  }
}
